
html {
  box-sizing: border-box;
  font-size: var(--fz);
  scroll-behavior: smooth;
}

*,
*::before,
*::after {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

body {
  // background: url('./../img/pattern.svg') top left;
  margin: 0;
  font-family: "Mont", sans-serif;
  font-weight: 400;
  color: var(--c-title);

  &.no-scroll {
    overflow-y: hidden;
  }
}
h1,h2,h3,h4,h5,h6{
  font-family: "Mont", sans-serif;
}
h1{
  font-family: "Mont", sans-serif;
  font-weight: 500;
  margin: 0;
  padding: 0;
  color: var(--c-title);
  font-size: 60px;
  line-height: 1.2;

  mark {
    margin: 0;
    padding: 0;
    color: var(--c-primary);
    background-color: transparent;
  }
  span{
    font-size: 55px;
    color: var(--c-white);
  }
}
h2{
  line-height: 1.5;
}
.nav {
  display: flex;
  flex-wrap: wrap;
  padding-left: 0;
  margin-bottom: 0;
  list-style: none;
}


h4,h5 {
  font-family: "Mont", sans-serif;
  font-size: var(--fz-xs);
  color: var(--c-primary);
  font-weight: 400;
  line-height: 1;
  margin-bottom: 1rem;
  text-transform: uppercase;
}

h2 {
  margin-bottom: var(--offset-m);
}


img {
  max-width: 100%;
}

a {
  font-family: "Mont", sans-serif;
  text-decoration: none;
  color: var(--c-title);
  font-size: var(--fz-s);
  transition: color .2s linear;
  letter-spacing: 0.2px;

  &:hover {
    color: var(--c-primary);
  }
}

p,li {
  color: var(--c-def);
  font-size: var(--fz-l);
  line-height: 1.2;
}

.container {
  margin: 0 auto;
  padding: 0 calc(var(--container-offset)) 0 var(--container-offset);
  max-width: var(--container-width);
}
.site-container {
}

.container--relative {
  position: relative;
}
