// }
.btn-reset {
  border: none;
  padding: 0;
  background: transparent;
  cursor: pointer;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.hover-line {
  position: relative;

  &::after {
    content: "";
    display: block;
    width: 100%;
    height: 1px;
    background: var(--c-primary);
    position: absolute;
    bottom: -2px;
    left: 0;
    transform: scaleX(0);
    transition: transform .2s ease-in;
  }

  &:hover {
    &::after {
      transform: scaleX(1);
    }
  }
}
.shadow-top{
  position: relative;
  &::before{
    content: '';
    top: 0;
    left: 0;
    width: 100%;
    height: 50%;
    display: block;
    position: absolute;
    z-index: 5;
    background: rgb(255,255,255);
    background: linear-gradient(0deg, rgba(255,255,255,0) 80%, rgba(0,0,0,1) 100%);
    opacity: 0.6;
  }
}
.shadow-bootom-left{
  position: relative;
  &::before{
    content: '';
    bottom: 0;
    left: 0;
    width: 100%;
    height: 50%;
    display: block;
    position: absolute;
    z-index: 1;
    background: rgb(255,255,255);
    background: linear-gradient(220deg, rgba(255,255,255,0) 49%, rgba(0,0,0,1) 100%);
    opacity: 0.5;
  }
}
  .shadow-bottom{
    transition: 0.3s linear;
    position: relative;
    &::before{
      content: '';
      bottom: 0;
      left: 0;
      width: 100%;
      height: 100%;
      display: block;
      position: absolute;
      z-index: 1;
      background: rgb(5,2,3);
      background: linear-gradient(211deg, rgba(5,2,3,0) 0%, rgba(127,49,72,0.700717787114846) 100%);
      background: linear-gradient(180deg, rgba(255,255,255,0) 0%, rgba(5,2,3,0.938813025210084) 100%);
    }
  }
.shadow-bottom-black{
  position: relative;
  &::before{
    content: '';
    bottom: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: block;
    position: absolute;
    z-index: 2;
    background: rgb(255,255,255);
    background: linear-gradient(180deg, rgba(255,255,255,0) 0%, rgba(5,2,3,0.938813025210084) 100%);
    background: linear-gradient(90deg, rgba(0,0,0,0.5046393557422969) 0%, rgba(0,0,0,0.4962359943977591) 100%);
  }
}
.shadow-right{
  position: relative;
  &::after{
    content: '';
    top: 0;
    right: 0;
    width: 50%;
    height: 100%;
    display: block;
    position: absolute;
    z-index: 1;
    background: rgb(255,255,255);
    background: linear-gradient(90deg, rgba(255,255,255,0) 50%, rgba(0,0,0,0.30015756302521013) 100%);
    opacity: 0.5;
  }
}
.link{
  position: relative;
  &.active{
    &::before{
      width: 100%;
    }
  }
  &-order{
    &:before{
      bottom: -8px !important;
    }
    &:hover{
      &:before{
        width: 0 !important;
      }
    }
  }
}
.link:before {
  content: "";
  display: block;
  width: 100%;
  height: 1px;
  background: var(--c-primary);
  position: absolute;
  bottom: -2px;
  left: 0;
  width: 0;
  transition: 0.3s ease-in;
}
.link:hover::before {
  width: 100%;
}


.section-caption{
  position: absolute;
  top: 0;
  bottom: 0;
  right: 0;
  font-size: 2.4em;
  text-align: center;
  text-transform: uppercase;
  font-weight: 700;
  text-orientation: mixed;
  -webkit-writing-mode: vertical-lr;
  -ms-writing-mode: tb-lr;
  writing-mode: vertical-lr;
  transform: translate(calc(100% - 100px));
  color: #e9f4f8;
  &.color{
    color: var(--c-white);
  }
}



.swiper-pagination,
.swiper-button-next, .swiper-button-prev{
  position: initial;
}
.swiper-pagination{
  width: auto;
  span{
    font-size: 16px;
  }
}
.swiper-button-next, .swiper-button-prev{
  width: 50px;
  &::after{
    content: none;
   
  }
  svg{
    transition: 0.3s linear;
    path{
      fill: var(--c-secondary);
      transition: 0.3s linear;
    }
  }
  &:hover{
    svg{
      path{
        fill: var(--c-primary);
      }
    } 
  }
}


.more{
  position: relative;
  display: flex;
  align-items: center;
  gap: 1rem;
  color: var(--c-primary);
  margin-top: auto;
  margin-bottom: 10px;
  span{
    position: relative;
    z-index: 1;
    transition: 0.3s linear;
  }
  svg{
    width: 40px;
    height: 13px;
    position: relative;
    path{
      transition: 0.3s linear;
      fill: var(--c-secondary);
    }
    &::before{
      content: "";
      position: absolute;
      width: 100%;
      height: 100%;
      top: 0;
      left: -100%;
      transition: 0.3s linear;
      background-color: var(--c-primary);
    }
  }

  &::before{
    content: '';
    position: absolute;
    top: 50%;
    left: 0;
    width: 0;
    height: 0;
    transform: translate(-50%,-50%);
    background-color: #e9f4f890;
    border-radius: 50%;
    z-index: 0;
    transition: 0.4s linear;
  }
  &:hover{
    &::before{
      width: 60px;
      height: 60px;
    }
    span{
      color: var(--c-secondary);
    }
    svg{
      path{
        fill: var(--c-primary);
      }
    }
  }
}

.wow{
  visibility: hidden;
}
 











